
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding-top: 40px;
}


body {
  background-color: #231f20;
  background-image: url(/static/img/pattern.6ca0740.png) !important;
  color: #9e830f !important;
}
#app {
  background-color: #231f20;
}
html, body {
  height: 100%;
}
h1, h2, h3 {
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
  font-weight: 500;
}
h1 {
  font-size: 23.58pt;
}
h4 {
  font-size: 18.05pt;
}
h4.background {
    position: relative;
    z-index: 1;
    text-align: left;
}
h4 span {
    background: #231f20;
    padding-right: 10px;
    text-align: center;
}
h4::before {
    border-top: 4px solid #9e830f;
    content: "";
    margin: 0 auto;
    position: absolute;
    top: 70%;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
}
.divider {
  border-top: 4px solid #9e830f;
}
.white {
  color: #fff;
}
a {
  font-family: 'Montserrat', sans-serif;
}
p {
  font-size: 17px;
  line-height: 1.5;
}
.navbar-default {
  background-color: transparent;
  border: none;
  border-top: 2px solid #9e830f;
  border-bottom: 2px solid #9e830f;
  margin-bottom: 40px;
  border-radius: 0px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
  -ms-border-radius: 0px;
  -moz-border-radius: 0px;
}
.navbar-default .navbar-toggle {
    border-color: #9e830f;
}
.navbar-default .navbar-toggle .icon-bar {
      background-color: #9e830f;
}
@media screen and (max-width: 768px) {
.navbar-default .navbar-collapse {
      border-color: #9e830f;
      background-color: #9e830f;
}
.navbar-default .navbar-collapse li a {
        color: #fff !important;
}
}
.navbar-default .navbar-nav {
    float: none;
}
@media screen and (min-width: 769px) {
.navbar-default li {
      float: none;
      display: inline-block;
      width: 15%;
}
}
.navbar-default li a {
    color: #9e830f !important;
    text-transform: uppercase;
    font-size: 12px;
}
.navbar-default .active {
    font-weight: 900;
}
.logo {
  width: 320px;
  margin-bottom: 30px;
}
footer {
  margin-top: 60px;
}
footer img {
    margin: 60px 0;
}
.modelwrapper {
  margin-bottom: 30px;
}
.modelwrapper .model {
    border: 1px solid #9e830f;
    text-transform: uppercase;
    padding: 10px;
    text-align: left;
}
.modelwrapper .model a {
      color: #9e830f;
}
.modelwrapper .model .title {
      font-family: 'EB Garamond', serif;
}
.modelwrapper .model .floorplan {
      font-weight: bold;
      font-family: 'Montserrat', sans-serif;
}
.modelwrapper .model p, .modelwrapper .model h3 {
      padding: 0 15px;
}
form {
  text-align: left;
  color: #fff;
}
form label {
    text-transform: uppercase;
}
.form-control {
  border-radius: 0px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
  -ms-border-radius: 0px;
  -moz-border-radius: 0px;
}
.btn-submit {
  background-color: transparent;
  color: #9e830f;
  padding: 10px 25px;
  border: 3px solid #9e830f;
  border-radius: 0px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
  -ms-border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-transition: background-color 0.3s ease-out;
  transition: background-color 0.3s ease-out;
  -ms-transition: background-color 0.3s ease-out;
}
.btn-submit:hover {
    background-color: #9e830f;
    color: #fff;
}
.preferredmethod {
  font-weight: bold;
  font-size: 18px;
}
select {
  width: 100%;
  height: 35px;
  border-radius: 0px;
  background-clip: padding-box;
  /* stops bg color from leaking outside the border: */
  -ms-border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: #e6e6e6;
  background-image: url(data:image/jpeg;base64,) !important;
  background-size: contain;
  background-position: right 50%;
  background-repeat: no-repeat;
  border: none !important;
}
@media screen and (max-width: 1600px) {
.blog {
    position: absolute;
    top: 0;
    right: 20% !important;
}
}
@media screen and (min-width: 1200px) {
li a {
    font-size: 15px !important;
}
}
@media screen and (max-width: 768px) {
.blog {
    position: absolute;
    top: 0;
    right: 0 !important;
}
}
.blog {
  position: absolute;
  top: 0;
  right: 30%;
  -webkit-transform: translateX(-30%);
  transform: translateX(-30%);
  cursor: pointer;
}


















































































































